import React from 'react';

const DashboardLayout = ({ children }) => {
    return (
        <div>

            <div className='grid grid-cols-12'>
                {/* side nav */}
                <div className='col-span-3 bg-blue-200 min-h-screen'>
                  <ul>
                    <li>add products</li>
                  </ul>
                </div>
                <div className='col-span-9'>
                     {children}
                </div>

            </div>
            
        </div>
    );
};

export default DashboardLayout;